<template>
  <div class="spyj-page">
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.lcslmc }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.rwmc }}
          </span>
        </div>
      </div>
    </div>
    <!-- 项目信息 end -->
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择退回任务</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-cell title="任务" :value="thrwVal" is-link @click="showThrwPopup" />
          <van-cell title="办理人员" :value="blryVal" is-link @click="showBlryPopup" />
          <div v-if="backResultMap.insertThfs==='insertThfs'">
            <div class="page-list">
              <div class="pagelist-left">退回提交方式</div>
              <div class="pagelist-right">{{ backResultMap.backSubmitTypeMap[backResultMap.backInfo.backSubmitType] }}</div>
            </div>
          </div>
          <div v-else>
            <van-cell title="退回提交方式" :value="thtjfsVal" is-link @click="showThtjfsPopup" />
          </div>
        </div>
      </div>

      <div v-if="dbxq.thyjbt!=null && dbxq.thyjbt!=3" class="content">
        <div class="common-main box">
          <div class="txyj">
            <div class="txyj-tit">
              <span v-if="dbxq.thyjbt!=null && dbxq.thyjbt===1" class="text-danger">*</span>
              {{ dbxq.taskCommentLabel }}
              <div class="label-text warn">不同意</div>
              <span class="more" @click="showCyyPopup()">选择常用语
                <i class="iconfont iconarrowright" />
              </span>
              <!--              <span class="more" @click="saveCyy()">保存常用语-->
              <!--              </span>-->
            </div>
            <div class="txyj-content">
              <textarea v-model="spyj" rows="3" :placeholder="'请输入'+dbxq.taskCommentLabel" />
            </div>
          </div>
        </div>
      </div>

      <div class="common-space" />
      <div class="foot-btn fixed">
        <ul>
          <!-- <li><van-button type="primary" plain round  @click="subimitSPYJ()">提交</van-button></li> -->
          <li><van-button type="info" size="large" round @click="subimitSPYJ()">不同意，退回本次审批</van-button></li>
        </ul>
      </div>
    </div>

    <transition name="router-slid" mode="out-in">
      <router-view />
    </transition>

    <!-- 退回任务popup beign -->
    <van-popup
      v-model="showThrw"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="backResultMap.tasks"
        value-key="name"
        @cancel="showThrw = false"
        @confirm="thrwOnConfirm"
      />
    </van-popup>
    <!-- 退回任务popup end -->

    <!-- 办理人员popup beign -->
    <van-popup
      v-model="showBlry"
      position="bottom"
      closeable
      :style="{ height: '60%' }"
    >
      <div class="common-popup">
        <div class="popup-title">请选择办理人员</div>
        <div class="popup-content">
          <div
            v-for="(ry, ryIndex) in backResultMap.blryList"
            :key="ryIndex"
          >
            <div :class="ry.isActive ? 'active select-renyuan': 'select-renyuan'" @click="selectRy(ry)">
              <div class="renyuan-l">
                <i class="iconfont iconxuanzhong" />
              </div>
              <div class="renyuan-m">
                <div class="name">{{ ry.name }}</div>
                <div class="info"><span>公司:{{ ry.orgName }}</span><span>部门:{{ ry.deptName }}</span></div>
              </div>
            </div>
          </div>
          <div class="common-space" />
        </div>

        <!-- 底部按钮 begin -->
        <div class="foot-btn fixed">
          <ul>
            <li><van-button type="info" round size="large" @click="blryOnConfirm">确定</van-button></li>
          </ul>
        </div>
      <!-- 底部按钮 end -->
      </div>

    </van-popup>
    <!-- 办理人员popup end -->

    <!-- 退回方式popup beign -->
    <van-popup
      v-model="showThfs"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="backResultMap.backSubmitTypeList"
        value-key="value"
        @cancel="showThfs = false"
        @confirm="thfsOnConfirm"
      />
    </van-popup>
    <!-- 退回方式popup end -->

    <!-- 选择常用语popup beign -->
    <van-popup
      v-model="showCyy"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="cyyList"
        value-key="nr"
        @cancel="showCyy = false"
        @confirm="xzCyyOnConfirm"
      />
    </van-popup>
    <!-- 选择常用语popup end -->

  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
import { mapState, mapMutations } from 'vuex'
import { getBackTaskListAction } from '../../../store/jbr/dbsx'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'DbsxBack',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      backResultMap: {},
      spyj: '',
      dbxq: {},
      headerData: {
        title: '待办审批',
        leftUrl: 'back',
        leftText: ' '
      },
      showThrw: false,
      showBlry: false,
      showThfs: false,
      yxryList: [],
      thrwVal: '选择退回任务',
      blryVal: '选择办理人员',
      thtjfsVal: '选择退回提交方式',
      blryids: '',
      toTaskDefineid: '',
      backSubmitType: '',
      showCyy: false,
      cyyList: [],
      pageno: 1, // 页
      totalpageno: 0 // 总页数
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      // listItem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid
    })

  },
  // watch: {
  // // 检测搜索条件发生变化，并进行搜索
  // cyy(newQuery) {
  //   // this.search(newQuery)
  //   this.spyj = newQuery
  // }
  // },
  created() {
    this.initTaskData()
    this.getDbxq()
    this.getcyylist()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_DBSX_CYY',
      'jbrdbsx/SET_Refresh'
    ]),
    initTaskData() {
      this.getBackTaskList()
    },
    getDbxq() {
      this.$store.dispatch('jbrdbsx/getDbxqAction', {
        bizid: this.txBizid // 单点登录传过来的执行人id
      }).then(res => {
        this.dbxq = res
      }).catch(() => {
      })
    },
    getcyylist() {
      this.$store.dispatch('jbrdbsx/getCyyListAction', {
        pageno: this.pageno,
        id: this.userInfo.id
      }).then(res => {
        this.cyyList = res.data
        this.pageno = res.pageno
        this.totalpageno = res.totalpageno
      }).catch(() => {
      })
    },
    saveCyy() {
      if (this.spyj != null && this.spyj.length !== 0) {
        this.$store.dispatch('jbrdbsx/saveCyyAction', {
          nr: this.spyj,
          ryid: this.userInfo.id
        }).then(res => {
          this.$toast('保存成功！')
          this.getcyylist()
        }).catch(() => {
        })
      } else {
        this.$toast(this.dbxq.taskCommentLabel + '为空，不能保存为常用语!')
      }
    },
    xzCyyOnConfirm(value) {
      this.spyj = value.nr
      this.showCyy = false
    },
    getBackTaskList() {
      getBackTaskListAction({
        id: this.txBizid
      }).then(res => {
        if (res.toTaskDefineidName) {
          this.thrwVal = res.toTaskDefineidName
        }
        if (res.blryids) {
          this.blryids = res.blryids
          this.blryVal = res.blrymcs
          this.yxryList = res.defaultRyList
        } else {
          this.blryVal = '选择办理人员'
          this.blryids = ''
          this.yxryList = []
        }
        if (res.backInfo.backSubmitType) {
          this.thtjfsVal = res.backSubmitTypeMap[res.backInfo.backSubmitType]
          this.backSubmitType = res.backInfo.backSubmitType
        }
        if (res.backInfo.toTaskDefineid) {
          this.toTaskDefineid = res.backInfo.toTaskDefineid
        }
        if (res.blryList) {
          for (let j = 0; j < res.blryList.length; j++) {
            res.blryList[j].isActive = false
          }
        }
        this.backResultMap = res
      })
    },
    thrwOnConfirm(value) {
      this.toTaskDefineid = value.id
      this.thrwVal = value.name
      this.showThrw = false
      this.$store.dispatch('jbrdbsx/getblryxxAction', {
        rwdyid: this.toTaskDefineid,
        lcslid: this.backResultMap.lcslid
      }).then(res => {
        if (res.blryList) {
          for (let j = 0; j < res.blryList.length; j++) {
            res.blryList[j].isActive = false
          }
          this.backResultMap.blryList = res.blryList
          this.backResultMap.actorMap = res.actorMap
          this.backResultMap.blryids = res.blryids
          this.backResultMap.blrymcs = res.blrymcs
          this.backResultMap.defaultRyList = res.defaultRyList
          this.backResultMap.actorsString = res.actorsString
          if (this.backResultMap.blryids) {
            this.blryids = this.backResultMap.blryids
            this.blryVal = this.backResultMap.blrymcs
            this.yxryList = this.backResultMap.defaultRyList
          } else {
            this.blryVal = '选择办理人员'
            this.blryids = ''
            this.yxryList = []
          }
        }
      }).catch(() => {
      })
    },
    selectRy(ry) {
      if (ry.isActive) {
        ry.isActive = false
        const index = this.yxryList.findIndex(item => {
          if (item.id === ry.id) {
            return true
          }
        })
        this.yxryList.splice(index, 1)
      } else {
        ry.isActive = true
        this.yxryList.push(ry)
      }
    },
    blryOnConfirm() {
      let xzActorsId = ''
      let xzActorsVal = ''
      const yxrylist = this.yxryList
      if (yxrylist) {
        for (let i = 0; i < yxrylist.length; i++) {
          if (i === yxrylist.length - 1) {
            xzActorsId += yxrylist[i].id
            xzActorsVal += yxrylist[i].name
          } else {
            xzActorsId += yxrylist[i].id + ','
            xzActorsVal += yxrylist[i].name + ','
          }
        }
      }
      this.blryids = xzActorsId
      if (xzActorsVal === '') {
        this.blryVal = '选择办理人员'
      } else {
        this.blryVal = xzActorsVal
      }
      this.showBlry = false
    },
    thfsOnConfirm(value) {
      this.backSubmitType = value.key
      this.thtjfsVal = value.value
      this.showThfs = false
    },
    subimitSPYJ() {
      this.clickBackSP()
    },
    clickBackSP() {
      let spyjValidate = true
      if (this.dbxq.thyjbt != null && this.dbxq.thyjbt === 1) {
        if (this.spyj === null || this.spyj.length === 0) {
          spyjValidate = false
        }
      }
      console.log('退回参数====', {
        executorid: this.txBizid,
        backComment: this.spyj,
        toTaskDefineid: this.toTaskDefineid,
        backSubmitType: this.backSubmitType,
        blryids: this.blryids
      })
      if (spyjValidate) {
        this.$store.dispatch('jbrdbsx/sendBackSP', {
          executorid: this.txBizid,
          backComment: this.spyj,
          toTaskDefineid: this.toTaskDefineid,
          backSubmitType: this.backSubmitType,
          blryids: this.blryids
          // eslint-disable-next-line no-unused-vars
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else {
        this.$toast('请设置审批意见')
      }
    },
    showCyyPopup() {
      if (this.cyyList.length === 0) {
        this.$toast('常用语为空，请手动输入')
        return
      } else {
        this.showCyy = true
      }
    },
    showThrwPopup() {
      this.showThrw = true
    },
    showBlryPopup() {
      for (let i = 0; i < this.backResultMap.blryList.length; i++) {
        this.backResultMap.blryList[i].isActive = false
        for (let j = 0; j < this.yxryList.length; j++) {
          if (this.backResultMap.blryList[i].id === this.yxryList[j].id) {
            this.backResultMap.blryList[i].isActive = true
          }
        }
      }
      this.showBlry = true
    },
    showThtjfsPopup() {
      this.showThfs = true
    }
  }

}

</script>

<style lang="css" scoped>
    .nav-bar{box-shadow: none;}
    .page-list{padding:0 12px;}
</style>
